<template>
  <div class="invoice-card" v-if="invoices['id']">
    <div class="row">
      <div class="col-md-6 print-container text-center">
        <a href="/" class="btn btn-primary">
          <i class="fad fa-caret-circle-left"></i>
        </a>
      </div>

      <div class="col-md-6 print-container text-center">
        <button class="btn btn-primary" @click="print">
          Cetak <i class="fad fa-print"></i>
        </button>
      </div>
    </div>

    <div class="invoice-title text-center">
      <img
        src="data:image/png;base64,"
        alt="Logo Beliayam.com"
      />
    </div>

    <div class="invoice-title text-center">
      <div id="main-title justify-content-center">
        <p>PT. BELI AYAM COM</p>
        <small
          >Office 8, Level 18-A Jl. Jend. Sudirman Kav. 52-53 SCBD LOT 28,
          Jakarta Selatan</small
        >
      </div>
    </div>

    <div class="invoice-title">
      <div id="main-title">
        <h4>FAKTUR</h4>
        <span class="text-dark font-weight-700">#{{ invoices["id"] }}</span>
      </div>

      <span id="date" class="text-dark">{{ invoices["created_at"] | formatDate }}</span>
    </div>

    <div class="invoice-details">
      <table class="invoice-table">
        <thead>
          <tr>
            <td>PRODUK</td>
            <td>UNIT</td>
            <td>HARGA</td>
          </tr>
        </thead>

        <tbody>
          <tr
            class="row-data"
            v-for="(invoice, index) in invoices['sales_item']"
            :key="index"
          >
            <td>{{ invoice["product_name"] }}</td>
            <td id="unit">{{ invoice["quantity"] }}</td>
            <td>{{ (invoice["basic_price"] + invoice["centralCommission"]) + invoice["partnerCommission"] | formatMoney }}</td>
          </tr>

          <!-- <tr class="row-data">
            <td>Cappucino <span>(small)</span></td>
            <td id="unit">2</td>
            <td>7.00</td>
          </tr> -->

          <tr class="calc-row">
            <td colspan="2"><small>Biaya Tambahan</small></td>
            <td>
              <small>{{ total_additional | formatMoney }}</small>
            </td>
          </tr>

          <tr class="calc-row">
            <td colspan="2">Total</td>
            <td>{{ grand_total | formatMoney }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="invoice-footer text-center">
      Terima Kasih Telah Berbelanja Nikmati Kemudahan Pembeliaan Di beliayam.com
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import moment from "moment";
moment.locale("id");

export default {
  name: "stockDownload",
  data() {
    return {
      invoices: [],
      total_additional: 0,
      grand_total: 0,
    };
  },
  methods: {
    async getInvoice() {
      return await axios
        .get(
          `${process.env.VUE_APP_BASE_HOST_API}/pay/${this.$route.query["key"]}/invoice`,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("jwt"),
            },
          }
        )
        .then((results) => {
          console.log(results["data"]);
          if (results["data"]["success"]) {
            this.invoices = results["data"]["data"]["invoices"];
            this.total_additional = results["data"]["data"]["total_additional"];
            this.grand_total = results["data"]["data"]["grand_total"];
          }
        });
    },
    print() {
      document.title = "Faktur " + this.invoices["id"];
      window.print();
    },
  },
  filters: {
    formatMoney(val) {
      let formatter = new Intl.NumberFormat("id-ID", {
        style: "currency",
        currency: "IDR",
      });
      return formatter.format(val);
    },
    formatDate() {
      return moment().format("Do MMMM YYYY H:mm");
    },
  },
  created() {
    this.getInvoice();
  },
};
</script>



