<template>
  
    <div class="invoice-box">

        <div class="row">
            <div class="col-md-10">
                <img class="logo-print" src="data:image/png;base64," alt="Icon Beliayam.com" />
            </div>
            <div class="col-md-2 print-container">
                <button class="btn btn-primary" @click="print">Cetak <i class="fad fa-print"></i></button>
            </div>
        </div>

        <div class="row mt-5">
            <div class="col-md-6">
                <p>

                    PT Beli Ayam Com<br />
                    Jl. Guru Muchtar, RT.01/RW.12 <br />
                    Kel. Cimahpar, Kec. Bogor Utara, Kota Bogor, Jawa Barat 16155
                </p>
            </div>
        </div>

        <header class="text-center mb-4">

            <h2>
                <b>Laporan Penjualan Mitra</b>
            </h2>

        </header>

        <div class="table-responsive" v-for="(sale, index) in sales['sales']" :key="index">
            <div>
                Tanggal: {{ sale['updated_at'] | formatDate}}
            </div>
            <div>
                <button class="btn btn-primary w-25" @click="printInvoice(sale['id'])"><i class="fad fa-print"></i> cetak</button>
            </div>
            <table class="table table-bordered">
                <thead>
                    <tr class="heading">
                        <th>No</th>
                        <th>Produk</th>
                        <th>Harga Dasar</th>
                        <th>Harga Jual Pelanggan</th>
                        <th>Pembayaran</th>
                        <th>Penjualan</th>
                        <th>Piutang Mitra</th>
                        <th>Pendapatan Mitra</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index2) in sale['sales_item']" :key="index2">
                        <td> {{ index2 + 1 }}</td>
                        <td> {{ item['product_name'] }} </td>
                        <td> {{ item['basic_price'] + item['centralCommission'] | formatMoney }}</td>
                        <td> {{ (item['basic_price'] + item['centralCommission']) + item['partnerCommission'] | formatMoney }} </td>
                        <td> {{ sale['type'] }} </td>
                        <td> {{ item['quantity'] }} </td>
                        <td> {{ (item['basic_price'] + sales['centralCommission']) * item['quantity'] | formatMoney }}</td>
                        <td> {{ (item['partnerCommission']) * item['quantity'] | formatMoney }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="5">Total</td>
                        <td> {{ sale['total_quantity'] }} </td>
                        <td> {{ sale['total_debt'] | formatMoney }}</td>
                        <td> {{ sale['total_commission'] | formatMoney }}</td>
                    </tr>
                </tfoot>
            </table>
        </div>
    </div>
</template>

<script>
import axios from "axios"
import moment from "moment"

moment.locale('id');    

export default {
    name: "SalesReportDownload",
    data(){
        return {
            sales: []
        }
    },
    methods:{
        printInvoice(id){
            window.open(`/pay/invoice?key=${id}`); 
        },
        async getSales(){

            let from = this.$route.query['from']
            let to = this.$route.query['to']

            var isDate = function(date) {
                return (new Date(date) !== "Invalid Date") && !isNaN(new Date(date));
            }

            // if(!from || isDate(from) == false || !to){

            //     this.$router.push({
            //         path:'/notFound',
            //     })
            // }

            return  await axios.get(`${process.env.VUE_APP_BASE_HOST_API}/sales-report?from=${from}&to=${to}`,{
                headers: {
                    "Authorization": "Bearer " + localStorage.getItem("jwt"),
                },
            })
                .then(results => {
                    if(results['data']['success']){
                        this.sales = results['data']['data']
                    }
                })
        },
        print(){
            document.title = "Laporan Penjualan Mitra"
            window.print()
        },
    },
    filters:{
        formatDate(date){
            return moment(date).format("Do MMMM YYYY");  
        },

        formatMoney(val){
            let formatter = new Intl.NumberFormat('id-ID', {
                style: 'currency',
                currency: 'IDR',
            });

            return formatter.format(val);
        }
    },
    created() {
        this.getSales()
    }
}
</script>



<style>
    .invoice-box{
        background-color: #fff !important;
        padding: 50px;
    }
    nav, .footer{
        margin-bottom: 3rem;
    }

    @media only screen and (max-width: 600px) {
        .iconDetails {
            width: 75px;
            height: auto;
        }
        .logo-print{
            width: 100%;
        }
    }
    @media print {
        nav, .footer, .print-container {
            visibility: hidden;
        }
        .invoice-box{
            margin-top: 0rem !important;
        }
    }
</style>